/**
 * Overrides.................Styleguide overrides
 * Custom header.............Header not included in styleguide
 *
 */
/*------------------------------------*    #Overrides
\*------------------------------------*/
.side-menu-layout__header--bidder {
  display: -ms-flexbox;
  display: flex;
  padding-right: 42px; }

.side-menu-layout__content-wrapper {
  height: calc(100vh - 70px);
  padding: 18px 0; }

.side-menu-layout__content {
  margin: 0 18px; }

.side-menu-layout__menu ul li a {
  -ms-flex-flow: nowrap;
      flex-flow: nowrap; }

/*------------------------------------*    #Custom header
\*------------------------------------*/
.header__text {
  color: #646464;
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: right;
  padding: 4px 32px 0 24px;
  font-weight: 600;
  font-size: 14px; }



